import { ThunkAction } from "redux-thunk";
import { IBaseAction, StoreType } from "store";
import { CommonAction } from "../reducers/commonReducer";
export enum CommonrType {
  COMMON_DIALOG_SHOW = "COMMON_DIALOG_SHOW",
}
// action type define

export interface CommonAction_ShowDialog extends IBaseAction {
  type: CommonAction.COMMON_DIALOG_SHOW;
  payload: {
    title: string;
    src: any;
    type: string;
    isOpen: boolean;
  };
}

export interface CommonAction_ShowFormDownload extends IBaseAction {
  type: CommonAction.FORM_DOWNLOAD_SHOW;
  payload: {
    data: any;
    isOpen: boolean;
  };
}
export type CommonActionType = CommonAction_ShowDialog | CommonAction_ShowFormDownload; // | any other action;

export const showDialog = (title: string, src: any, type: string, isOpen: boolean): ThunkAction<void, StoreType, undefined, CommonActionType> => async (dispatch) => {
  dispatch({
    type: CommonAction.COMMON_DIALOG_SHOW,
    payload: { title, src, type, isOpen },
  });
};

export const showFormDownload = (data: any, isOpen: boolean): ThunkAction<void, StoreType, undefined, CommonActionType> => async (dispatch) => {
  dispatch({
    type: CommonAction.FORM_DOWNLOAD_SHOW,
    payload: { data, isOpen },
  });
};