import MenuItem from "@material-ui/core/MenuItem";
import Email from "@material-ui/icons/Email";
// import KeyboardArrowDownIcon from "@material-ui/icons/KeyboardArrowDown";
import useWindowSize from "utils/resizeViewport";
import logoBlack from "assets/images/slider/logo-black.png";
import logo from "assets/images/slider/logo.png";
// Mobile
import mlogoBlack from "assets/images/slider/mobile-logo-black.png";
import mlogo from "assets/images/slider/mobile-logo-white.png";
import iconVimeo from "assets/images/link/vimeo.png";
import iconLinkedin from "assets/images/link/linkedin.png";
import React, { useState } from "react";
import { Link } from "react-router-dom";
import "./Slider.scss";
import { Select } from "@material-ui/core";

const Slider: React.FC = () => {
  const [isShow, setIsShow] = useState<Boolean>(false);
  const [isFirst, setIsFirst] = useState<Boolean>(true);
  const [isClicking, setIsClicking] = useState<Boolean>(false);
  const [isShowList] = useState<Boolean>(false);

  const [width] = useWindowSize();
  const onShow = () => {
    if (!isClicking) {
      setIsShow(!isShow);
      setIsFirst(false);
      setIsClicking(true);

      setTimeout(() => {
        setIsClicking(false);
      }, 500);
    }
  };
  const closeSlider = () => {
    setIsShow(false);
  };
  // const onShowList = () => {
  //   setShowList(!isShowList);
  // };

  const checkSizeDevice = () => {
    return width < 768;
  };

  return (
    <>
      <div className={`com-slider ${isShow ? "opened" : ""}`}>
        <div className="slider-left">
          {/* Icon Logo back to homepage */}
          <Link to="/" onClick={closeSlider}>
            <img
              className="logo-white"
              src={checkSizeDevice() ? mlogo : logo}
              alt="logon-slider"
            />
            <img
              className="logo-black"
              src={checkSizeDevice() ? mlogoBlack : logoBlack}
              alt="logon-slider-black"
            />
          </Link>
          {/* Icon Menu Hambuger */}
          <div className="block-control">
            <input
              type="checkbox"
              id="checkbox3"
              className="checkbox3 visuallyHidden"
              checked={!!isShow}
              onChange={() => { }}
            />
            <label htmlFor="checkbox3" onClick={onShow}>
              <div className="hamburger hamburger3">
                <span className="bar bar1"></span>
                <span className="bar bar2"></span>
                <span className="bar bar3"></span>
                <span className="bar bar4"></span>
              </div>
            </label>
          </div>
          <Select defaultValue="eng" className="ul-lang opa-0 hidden">
            <MenuItem value="eng">English</MenuItem>
            <MenuItem value="fr">French</MenuItem>
          </Select>
        </div>

        <div
          className={`slider-right animated ${isFirst ? "" : "active"} ${
            isShow ? "showing" : ""
            }`}
        >
          <div className="wrapper">
            <div className="label">Menu</div>
            <div className="list-menu">
              <ul>
                <li>
                  <Link to="/who-we-are" onClick={onShow}>
                    Who we are
                  </Link>
                </li>

                <li className={isShowList ? "showing" : ""}>
                  <Link to="/offer" onClick={onShow}>
                    Our offer
                  </Link>
                  {/* <KeyboardArrowDownIcon
                    className="li-arrow"
                    onClick={onShowList}
                  /> */}
                </li>
                {/* <div className="list-subcate">
                  <Link to="/offer/advertising-solutions" onClick={onShow}>
                    <div>Advertising solutions</div>
                  </Link>
                  <Link to="/offer/gamification-services" onClick={onShow}>
                    <div>Gamification services</div>
                  </Link>
                  <Link to="/offer/partnerships-esports" onClick={onShow}>
                    <div>Partnerships & Esports</div>
                  </Link>
                </div>
                <li>
                  <Link to="/best-case" onClick={onShow}>
                    Best Cases
                  </Link>
                </li> */}
                <li>
                  <Link to="/contact-us" onClick={onShow}>
                    Contact us
                  </Link>
                </li>
                {/* <li className="news-studies">
                  <Link to="/news&study" onClick={onShow}>
                    News & Studies
                  </Link>
                </li> */}
              </ul>
            </div>
            <div className="block-contact">
              <div className="title">
                <Link to="/contact-us" onClick={onShow}>
                  Contact us
                </Link>
              </div>
              <div>
                <div className="title">Follow us :</div>
                <div className="content link">
                  <a
                    href="https://www.linkedin.com/company/gameloft-advertising-solutions"
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    <img src={iconLinkedin} alt="iconLinkedin" />
                  </a>
                  <a
                    href="https://vimeo.com/user45573395"
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    <img src={iconVimeo} alt="iconVimeo" />
                  </a>
                </div>
              </div>
            </div>
            <div className="block-contact-link">
              <div className="content link">
                <a
                  href="https://www.linkedin.com/company/gameloft-advertising-solutions"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <img src={iconLinkedin} alt="iconLinkedin" />
                </a>
                <a
                  href="https://vimeo.com/user45573395"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <img src={iconVimeo} alt="iconVimeo" />
                </a>
              </div>
              {/* <Select defaultValue="eng" className="ul-lang" IconComponent={() => <KeyboardArrowDownIcon fontSize="small" />}>
                <MenuItem value="eng">English</MenuItem>
                <MenuItem value="fr">French</MenuItem>
              </Select> */}
            </div>
          </div>
        </div>
      </div>

      <div className="float-contact">
        <div className="content link">
          <Link to="/contact-us" className="contact">
            <span className="hint">Contact Us</span>
            <Email></Email>
          </Link>
          <a
            href="https://www.linkedin.com/company/gameloft-advertising-solutions"
            target="_blank"
            rel="noopener noreferrer"
            className="linkedin"
          >
            <span className="hint">LinkedIn</span>
            <img src={iconLinkedin} alt="iconLinkedin" />
          </a>
          <a
            href="https://vimeo.com/user45573395"
            target="_blank"
            rel="noopener noreferrer"
            className="vimeo"
          >
            <span className="hint">Vimeo</span>
            <img src={iconVimeo} alt="iconVimeo" />
          </a>
        </div>
      </div>
    </>
  );
};

export default Slider;
