import btnClose from "assets/images/contact/btn-close.png";
//import axios from "axios";
import React, { createRef, useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import { StoreType } from "store";
import { showFormDownload } from "store/actions/commonAction";
import { FormDownloadData } from "store/reducers/commonReducer";
//import Swal from "sweetalert2";
import { RECAPTCHA_SITEKEY } from "utils";
import { recaptcha3 } from "utils/recaptcha/ReCaptchaV3";
import "./FormDowload.scss";

type FormData = {
  name: string;
  email: string;
  company: string;
  job: string;
  token: string;
};
const FormDowload: React.FC = (props) => {
  const { register, handleSubmit, errors } = useForm<FormData>({
    mode: "onBlur",
  });
  const formRef = createRef<HTMLFormElement>();

  const onSubmit = async (data: FormData) => {
    const captcha = await recaptcha3.execute(RECAPTCHA_SITEKEY, "ContactForm");
    data = { ...data, token: captcha };
    formRef.current?.reset();

    // axios
    //   .post(`${window.APP_API_URL}/api/user/contact`, data)
    //   .then(function (response) {
    //     setTimeout(() => {
    //       Swal.fire("Send mail successfully", "Thank you for contacting us", "success");
    //     }, 500);
    //   })
    //   .catch(function (error) {
    //     setTimeout(() => {
    //       Swal.fire("Send mail failed", "Some things went wrong..", "error");
    //     }, 500);
    //   });
  };
  const [open, setOpen] = useState(false);
  const isValid = () => Object.keys(errors).length !== 0;
  const formDownload = useSelector<StoreType, FormDownloadData | undefined>((state) => state.common.formDownload);
  const dispatch = useDispatch();
  useEffect(() => {
    if (formDownload && formDownload.data !== undefined) {
      setOpen(formDownload.isOpen);
    }

  }, [formDownload]);
  return (
    <section className={`com-form-download animated faster ${open ? "fadeInDown visible" : "fadeOutDown"}`}>
      <img
        src={btnClose}
        alt="btnClose"
        onClick={() => {
          setOpen(false);
          dispatch(showFormDownload(null, false));
        }}
      />

      <h2 className="title">Download Study</h2>
      <form ref={formRef} onSubmit={handleSubmit(onSubmit)}>
        <div className="item">
          <span className="label">Full name *</span>
          {errors.name && <span className="error animated fadeIn faster">Name is required</span>}
          <input type="text" name="name" placeholder="Your first and last name" ref={register({ required: true })} />
        </div>

        <div className="item">
          <span className="label">Email address *</span>
          {errors.email && errors.email.type === "required" && <span className="error animated fadeIn faster">Email is required</span>}
          {errors.email && errors.email.type === "pattern" && <span className="error animated fadeIn faster">Invalid email</span>}
          <input
            type="text"
            name="email"
            placeholder="Your business email address"
            ref={register({
              required: true,
              pattern: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i,
            })}
          />
        </div>

        <div className="item">
          <span className="label">Company *</span>
          {errors.company && <span className="error animated fadeIn faster">Your company name is required</span>}
          <input type="text" name="company" placeholder="Your company name" ref={register({ required: true })} />
        </div>

        <div className="item">
          <span className="label">Job title</span>
          <input type="text" name="job" placeholder="Your job title" ref={register()} />
        </div>

        <button className="btn-submit" disabled={isValid()}>
          Download
        </button>
      </form>
    </section>
  );
};

export default FormDowload;
