import { CommonActionType } from "store/actions/commonAction";

export enum CommonAction {
  GET_DATA = "GET_DATA", // get data for page or view base on current language
  CHANGE_LANGUAGE = "CHANGE_LANGUAGE",
  SET_PAGE = "SET_PAGE",
  COMMON_DIALOG_SHOW = "COMMON_DIALOG_SHOW",
  FORM_DOWNLOAD_SHOW = "FORM_DOWNLOAD_SHOW",
}
export interface DialogData {
  type: string;
  src: any;
  title?: string;
  isOpen: boolean;
}
export interface FormDownloadData {
  isOpen: boolean;
  data: any;
}
export interface ICommonState {
  dialog: DialogData;
  formDownload: FormDownloadData;
}

const defaultCommonState = {
  type: "",
  src: "",
  title: "",
  isOpen: false,
};
const defaultFormDownloadState = {
  isOpen: false,
  data: null,
};

const initialState: ICommonState = {
  dialog: defaultCommonState,
  formDownload: defaultFormDownloadState,
};

const commonReducer = (state: ICommonState = initialState, action: CommonActionType) => {
  switch (action.type) {
    case CommonAction.COMMON_DIALOG_SHOW:
      return { ...state, dialog: action.payload };
      
    case CommonAction.FORM_DOWNLOAD_SHOW:
      return { ...state, formDownload: action.payload };
    default:
      return state;
  }
};

export default commonReducer;
